/* src/pages/HomePage.css */
:root{
  --brand:#2563eb;
  --brand-dark:#1d4ed8;
  --muted:#6b7280;
  --radius:10px;
  --shadow:0 2px 10px rgba(0,0,0,.08);
}

/* Page grid */
.page{
  display:grid;
  gap:24px;
  padding:24px;
}

/* Hero */
.hero{
  background: linear-gradient(135deg, var(--brand) 0%, var(--brand-dark) 100%);
  color:#fff;
  border-radius:var(--radius);
  box-shadow:var(--shadow);
  padding:28px;
}
.hero-text h1{
  margin:0 0 8px;
  font-size:2rem;
  font-weight:800;
}
.hero-text p{
  margin:0 0 16px;
  opacity:.95;
}
.hero-actions{
  display:flex;
  gap:12px;
  flex-wrap:wrap;
}

/* Buttons */
.btn{
  display:inline-flex;
  align-items:center;
  gap:8px;
  padding:10px 14px;
  border:1px solid #d1d5db;
  border-radius:8px;
  background:#fff;
  color:#111827;
  cursor:pointer;
  text-decoration:none;
  transition:background .15s ease, border-color .15s ease;
}
.btn:hover{ background:#f3f4f6; }
.btn.primary{
  background:var(--brand);
  border-color:var(--brand);
  color:#fff;
}
.btn.primary:hover{ background:var(--brand-dark); }

/* Card sections */
.section{
  background:#fff;
  border-radius:var(--radius);
  box-shadow:var(--shadow);
  padding:20px;
}
.section h2{
  margin:0 0 12px;
  font-size:1.25rem;
}

/* Section header with tools */
.section-head{
  display:flex;
  align-items:center;
  justify-content:space-between;
  gap:12px;
  margin-bottom:12px;
}
.tools{
  display:flex;
  gap:10px;
  align-items:center;
}
.search{
  min-width:220px;
  padding:8px 10px;
  border:1px solid #d1d5db;
  border-radius:8px;
  outline:none;
}
.search:focus{
  border-color:var(--brand);
  box-shadow:0 0 0 3px rgba(37,99,235,.12);
}

/* Responsive */
@media (max-width: 720px){
  .page{ padding:16px; gap:18px; }
  .hero{ padding:22px; }
  .section{ padding:16px; }
  .hero-text h1{ font-size:1.6rem; }
}
