.register {
  width: 100%;
  max-width: 420px;
  margin: 40px auto;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 10px rgba(0,0,0,.08);
  padding: 24px;
}
.register h2 { margin: 0 0 16px; font-size: 1.4rem; }
.register-form { display: grid; gap: 12px; }
.field { display: grid; gap: 6px; }
label { font-size: .9rem; color: #6b7280; }
input[name="username"],input[type="text"], input[type="email"], input[type="password"] {
  padding: 10px 12px; border: 1px solid #d1d5db; border-radius: 6px; font-size: 1rem;
}
.submit { margin-top: 8px; padding: 10px 14px; background: #2563eb; color: #fff; border: none; border-radius: 6px; cursor: pointer; }
.submit:hover { background: #1d4ed8; }
.hint { margin-top: 12px; color: #6b7280; font-size: .9rem; text-align: center; }
